import React from 'react';
import { graphql, Link } from "gatsby"
import ReactFullpage from '@fullpage/react-fullpage';
import { Canvas } from 'react-three-fiber'
import Layout from "../components/layout"
import SEO from "../components/seo"
import { Container, Row, Col } from "react-bootstrap"
import SlickSlider from "../components/slickSlider"

import VisualThinkingIcon from "../images/icon-visual-thinking.inline.svg";
import DesignDevIcon from "../images/icon-design-dev.inline.svg";
import BuiltIcon from "../images/icon-built.inline.svg";
import DownArrow from "../images/down-arrow.inline.svg";

//import { Times, Heart, Comment } from "../../components/Icons";



class FullPage extends React.Component {

  

  constructor(props) {
    super(props);
    this.pageData = this.props.data.allWordpressPage.edges[0].node.acf
    this.workData = this.props.data.allWordpressWpWork.edges
  }

  onLeave(origin, destination, direction) {
    console.log("Leaving section " + origin.index);
  }

  afterLoad(origin, destination, direction) {
    console.log("After load: " + destination.index);
  }
  render() {
    
    return (
      <Layout pageInfo={{ pageName: "index" }}>
      <SEO title="Home" keywords={[`gatsby`, `react`, `bootstrap`]} />
      <ReactFullpage
        navigation
        normalScrollElements= '.project-list'
        anchors={['welcome', 'expertise', 'selected-projects', 'client-list', 'contact']}
        licenseKey={'75AEE7FB-1CA04072-A388ED7C-844E24BC'}
        onLeave={this.onLeave.bind(this)}
        afterLoad={this.afterLoad.bind(this)}
        render={({ state, fullpageApi }) => {

          return (
            <div id="fullpage-wrapper">

              <section className="section section-content section-welcome">
                <Container>
                <h2 className="section-title">Welcome</h2>
                <div dangerouslySetInnerHTML={{ __html: this.pageData.welcome_intro }} />
                <button className="down-btn" onClick={() => fullpageApi.moveTo(2,0)}>
                  <DownArrow/>
                </button>
                </Container>
              </section>

              <section className="section section-content section-expertise">
                <Container>
                  <h2 className="section-title">Expertise</h2>
                  <Row className="expertise-row">
                    <div className="col-md-4">
                      <div className="expertise-wrapper">
                        <div className="expertise-icon">
                          <VisualThinkingIcon />
                        </div>
                        <h2 className="section-title">{this.pageData.expertise_block_1.title}</h2>
                        <h1>{this.pageData.expertise_block_1.call_out_title}</h1>                        
                        <div dangerouslySetInnerHTML={{ __html: this.pageData.expertise_block_1.content }} />
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="expertise-wrapper">
                        <div className="expertise-icon">
                          <DesignDevIcon />
                        </div>
                        <h2 className="section-title">{this.pageData.expertise_block_2.title}</h2>
                        <h1>{this.pageData.expertise_block_2.call_out_title}</h1>
                        <div dangerouslySetInnerHTML={{ __html: this.pageData.expertise_block_2.content }} />
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="expertise-wrapper">
                        <div className="expertise-icon">
                          <BuiltIcon />
                        </div>
                        <h2 className="section-title">{this.pageData.expertise_block_3.title}</h2>
                        <h1>{this.pageData.expertise_block_3.call_out_title}</h1>
                        <div dangerouslySetInnerHTML={{ __html: this.pageData.expertise_block_3.content }} />
                      </div>
                    </div>
                  </Row>                  
                </Container>
              </section>

              <section className="section section-content section-projects">
                <Container>
                <h2 className="section-title">Selected projects</h2>
                <ul className="project-list">
                  {this.workData.map(work => (
                    <li>
                      <Link
                        to={`/projects/${work.node.slug}`}
                      >
                        <h3
                            dangerouslySetInnerHTML={{ __html: work.node.title }}
                          />
                      </Link>
                    </li>
                  ))}
                </ul>
                </Container>
              </section>

              <section className="section section-content section-testimonials">
                <Container>
                <h2 className="section-title">What Clients Say</h2>
                <SlickSlider 
                  data={this.pageData}
                />
                </Container>
              </section>

              <section className="section section-content section-contact">
                <Container>
                <h2 className="section-title">Contact</h2>
                <div className="contact-wrapper">
                  <a href="mailto:hello@visual23.com">hello@visual23.com</a>
                  <div className="break" />
                  <a href="tel:8282256670">828.225.6670</a>
                </div>
                </Container>
              </section>

            </div>            
          );
        }}
      />
      </Layout>
    );
  }
}

export const query = graphql`
query{
  allWordpressPage(filter: {wordpress_id: {eq: 4}}) {
    edges {
      node {
        acf {
          welcome_intro
          expertise_block_1 {
            call_out_title
            content
            title
          }
          expertise_block_2 {
            call_out_title
            content
            title
          }
          expertise_block_3 {
            call_out_title
            content
            title
          }
          testimonials{
            comment
            from
          }
        }
      }
    }
  }
  allWordpressWpWork{
    edges{
      node{
        title
        slug
      }
    }
  }
}
`

export default FullPage;
