import React, { Component } from "react";
import Slider from "react-slick";

import ArrowNext from "../images/arrow-right.inline.svg";
import ArrowPrev from "../images/arrow-left.inline.svg";

function NextArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      onClick={onClick}
    >
      <ArrowNext/>
    </div>
  );
}

function PrevArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      onClick={onClick}
    >
      <ArrowPrev/>
    </div>
  );
}

const SlickSlider = ({ data }) => {
  //console.log("databug = "+JSON.stringify(data))

  const settings = {
      dots: false,
      infinite: true,
      fade: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
    }
  return (
    <>
      <Slider {...settings}>
        {data.testimonials.map(testimonial => (
          <div className="testimonial-wrapper">
            <div className="testimonial-comment"
              dangerouslySetInnerHTML={{ __html: testimonial.comment }}
            />
            <h3
              dangerouslySetInnerHTML={{ __html: testimonial.from }}
            />
          </div>
        ))}
      </Slider>
    </>
  )
}

export default SlickSlider